</template>
<script>
import functionsFirebase from "../functionsFB"
import Participants from "./Participants.vue"

export default {
  components: { Participants },
  data() {
    return {
      idMeet: null,
      userId: null,
      isHost: false,
      isMuted: false,
      isVideo: false,
      btnEnd: null,
      btnLeave: null,
      btnMuteAudio: null,
      btnUnMuteAudio: null,
      btnMuteVideo: null,
      btnUnMuteVideo: null,
      participants: [],
      tabActive: 1,
    }
  },
  created() {
    const params = this.$route.query
    this.idMeet = params.idMeet
    this.userId = params.userId
    this.isHost = params.isHost
  },
  mounted() {
    this.btnEnd = document.querySelector("#btn-end")
    this.btnLeave = document.querySelector("#btn-leave")
    if (this.isHost) {
      this.btnLeave.style.display = "none"
    } else {
      this.btnEnd.style.display = "none"
    }

    this.btnMuteAudio = document.querySelector("#muteAudio")
    this.btnUnMuteAudio = document.querySelector("#unMuteAudio")
    this.btnMuteAudio.style.display = "none"
    this.btnMuteVideo = document.querySelector("#muteVideo")
    this.btnUnMuteVideo = document.querySelector("#unMuteVideo")
    this.btnMuteVideo.style.display = "none"

    this.loadParticipants()
  },
  methods: {
    unMuteAudio() {
      console.log("unmute audio")
      this.isMuted = false
      // this.btnMuteAudio.style.display = "block"
      // this.btnUnMuteAudio.style.display = "none"
      localStorage.setItem("mic", true)
    },
    muteAudio() {
      console.log("mute audio")
      this.isMuted = true
      // this.btnMuteAudio.style.display = "none"
      // this.btnUnMuteAudio.style.display = "block"
      localStorage.setItem("mic", true)
    },
    unMuteVideo() {
      console.log("unmute video")
      this.isVideo = false
      // this.btnMuteVideo.style.display = "block"
      // this.btnUnMuteVideo.style.display = "none"
      localStorage.setItem("cam", true)
    },
    muteVideo() {
      console.log("mute video")
      this.isVideo = true
      // this.btnMuteVideo.style.display = "none"
      // this.btnUnMuteVideo.style.display = "block"
      localStorage.setItem("cam", true)
    },
    leave() {
      console.log("leave")
      localStorage.setItem("isLeave", true)
      localStorage.setItem("isHost", false)
    },
    end() {
      localStorage.setItem("isLeave", true)
      localStorage.setItem("isHost", true)
    },
    config() {
      console.log("config", this.zoomMtg)
    },
    async loadParticipants() {
      const participantsRef = await functionsFirebase.prototype.getAllDocsWithCollection(`Meetings/${this.idMeet}/attendees`)
      this.participants = participantsRef.docs.map((doc) => {
        return {
          id: doc.id,
          ...doc.data(),
        }
      })
    },
  },
}
</script>
<style scoped>
