  color: #dadada;
}

.fullWidth {
  width: 100vw;
}

.nav-link {
  color: #000;
}
.activeTab {
  color: #164ff5;
  border-bottom: 2px solid #164ff5;
}

[class^="clg-"],
[class*=" clg-"] {
  vertical-align: initial !important;
}
</style>
